import React from "react";
import { Outlet } from "react-router-dom";
import styled from "styled-components";
import RequireAuth from "../auth/RequiredAuth";
import { useSelector } from "react-redux";
import useCookies from "react-cookie/cjs/useCookies";
const TopNavbar = React.lazy(() => import("../layout/NavigationBarV2"));
const SideNavbar = React.lazy(() => import("../layout/SideNavigationBar"));

const Teacher = ({ children }) => {
  const { title } = useSelector((state) => state.navBarTitle);
  // eslint-disable-next-line no-unused-vars
  const [cookie, setCookie, removeCookie] = useCookies(["log"]);

  React.useEffect(() => {
    const headerTitle = title + " - Bangladesh Open University";
    document.title = headerTitle;
  }, [title]);

  return (
    <RequireAuth>
      <Panel>
        <ASide id="side-nav">
          <SideNavbar />
        </ASide>
        <Body>
          <TopNavbar />
          <ChildrenPanel>
            {children}
            <Outlet />
          </ChildrenPanel>
        </Body>
      </Panel>
    </RequireAuth>
  );
};

export default Teacher;

const Panel = styled.div`
  width: 100%;
  display: grid;
  height: 100vh;
  grid-template-columns: 100vw;
  position: relative;
  @media (max-width: 900px) {
    display: flex;
  }
`;

const ASide = styled.aside`
  overflow: hidden;
  transition: all ease-in 1s;
  position: fixed;
  width: 300px;
  display: none;
  background: #2f3542;
  color: #fff;
  z-index: 2;
  left: 0.5rem;
  top: 1px;
  bottom: 2px;
  box-shadow: 1px 1px 5px 5px rgba(3, 3, 3, 0.5);

  @media (max-width: 820px) {
    width: 98%;
    right: 0.5rem;
  }
`;

const Body = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  overflow: hidden;
`;

const ChildrenPanel = styled.div`
  width: 100%;
  overflow-y: auto;
`;
